.main-logo {
    font-family: Arial, sans-serif;
    font-size: 1.5rem;
    color: white;
    font-weight: 700;
    padding: 16px 21px;
    margin-right: 20px;
}

.main-message {
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-family: Arial, sans-serif;
    font-size: 20px;
    line-height: 34px;
    color: white;
    padding: 15px 25px 15px 25px;
}

.toolbar-container {
    display: flex;
    position: relative;
    flex-direction: row;
    justify-content: center;
    background-color: #282c34;
    height: 100px;
    width: 100%;
}

.toolbar-btn {
    font-weight: bold;
    justify-content: center;
    background-color: transparent;
    border: none;
    color: rgb(255, 255, 255);
    padding: 14px 21px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 8px 2px;
    cursor: pointer;
}

.toolbar-btn:hover {
    color: blueviolet;
}

.toolbar-btn-running {
    font-weight: bold;
    justify-content: center;
    background-color: transparent;
    border: none;
    color: rgb(182, 182, 182);
    padding: 14px 21px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 8px 2px;
}

.button {
    font-weight: bold;
    background-color: #282c34;
    border: none;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
}
